/* src/index.css */

/* Стили для общего фона */
body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f0f2f5;
  color: #333;
}

/* Центрируем контейнер */
.container {
  max-width: 1200px;
  margin: 20px auto;
  padding: 20px;
}

/* Стили для заголовков */
h1 {
  text-align: center;
  color: #333;
}

/* Стили для списка пользователей */
ul {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
  list-style: none;
  padding: 0;
}

li {
  background-color: #fff;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
}

li:hover {
  transform: translateY(-5px);
}

a {
  text-decoration: none;
  font-size: 18px;
  font-weight: bold;
  color: #007bff;
  transition: color 0.2s ease;
}

a:hover {
  color: #0056b3;
}

/* Стили для комментариев */
.comment {
  background-color: #fff;
  padding: 20px;
  margin: 20px 0;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.comment strong {
  font-size: 18px;
  color: #555;
}

.comment p {
  font-size: 16px;
  line-height: 1.6;
}

/* Кнопки */
button {
  display: inline-block;
  background-color: #007bff;
  color: #fff;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.2s ease;
}

button:hover {
  background-color: #0056b3;
  transform: scale(1.05);
}
